@charset "UTF-8";
/*header */
header.home {
  position: absolute;
  width: 100%;
  z-index: 10;
}

header.sub-pages {
  background-color: #000000;
}

.top-header {
  padding: 15px 0 0 0;
  background-color: #ffffff;
}
.top-header a, .top-header .footer ul address, .footer ul .top-header address {
  color: #707070;
  text-decoration: none;
}
.top-header a:hover, .top-header .footer ul address:hover, .footer ul .top-header address:hover {
  color: #F1C40F;
}
.top-header ul {
  text-align: right;
}
.top-header ul li::after {
  content: " |";
}
.top-header ul li:last-child::after {
  content: "";
}

/*mid-header*/
.mid-header .navbar-light .navbar-nav {
  margin: 0 auto;
}
.mid-header .navbar-light .navbar-nav .nav-link {
  color: #ffffff;
}
.mid-header .navbar-light .navbar-nav .nav-link:hover {
  color: #F1C40F;
}
.mid-header .navbar-light .navbar-toggler {
  border-color: transparent;
}
.mid-header .bgcolor-black {
  background-color: #000000;
}

ul.header-icons {
  margin-top: 15px;
}
ul.header-icons li {
  position: relative;
}
ul.header-icons li a, .footer ul.header-icons li address {
  color: #ffffff;
  font-size: 20px;
}
ul.header-icons li a:hover, .footer ul.header-icons li address:hover {
  color: #F1C40F;
}

.box {
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  left: 50%;
}
.box .input {
  padding: 10px;
  width: 40px;
  height: 30px;
  background: none;
  border: none;
  border-radius: 50px;
  box-sizing: border-box;
  font-size: 20px;
  color: #F1C40F;
  outline: none;
  transition: 0.5s;
}
.box .input:hover {
  width: 245px;
  background: #ffffff;
  border-radius: 10px;
}
.box i {
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  right: 15px;
  font-size: 20px;
  color: #ffffff;
  transition: 0.2s;
}
.box i:hover {
  opacity: 0;
  z-index: -1;
}

/*footer*/
.footer {
  background-color: #000000;
  padding: 50px 0;
}
.footer img {
  width: 50%;
  height: 70px;
}
.footer h5 {
  color: #F1C40F;
}
.footer p.footer-description {
  padding-top: 20px;
  color: #ffffff;
}
.footer ul {
  padding-left: 0;
}
.footer ul li {
  display: block;
}
.footer ul li i {
  color: #ffffff;
  font-size: 18px;
  padding-right: 5px;
}
.footer ul a, .footer ul address {
  font-size: 18px;
  color: #ffffff;
  text-decoration: none;
}
.footer ul a:hover, .footer ul address:hover {
  color: #F1C40F;
}
.footer .social-media li {
  padding-right: 10px;
  display: inline-block;
}

